import React, { Component } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Col } from 'reactstrap';
import classnames from 'classnames';
import './dashboard.css';
import { AddStory } from '../add-story/add-story';
import { ViewStory } from '../view-story/view-story';

export class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: 'viewStory'
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
  render() {
    return (
      <section className="dashboard">
        <Col xs="12" className="p-0 float-left">
          <Nav tabs className="side-nav">
            <NavItem className={classnames({ active: this.state.activeTab === 'viewStory' })} onClick={() => { this.toggle('viewStory'); }}>
              <NavLink>
                View Stories
              </NavLink>
            </NavItem>
            <NavItem className={classnames({ active: this.state.activeTab === 'addStory' })} onClick={() => { this.toggle('addStory'); }}>
              <NavLink>
                Add New Story
              </NavLink>
            </NavItem>
          </Nav>
        </Col>
        <Col xs="12" className="float-left">
          <TabContent activeTab={this.state.activeTab}>
            <TabPane tabId="viewStory">
              <ViewStory/>
            </TabPane>
            <TabPane tabId="addStory">
              <AddStory/>
            </TabPane>
          </TabContent>
        </Col>
      </section>
    );
  }
}