import React, { Component } from 'react';
import { Col, Button } from 'reactstrap';
import './view-story.css';

export class ViewStory extends Component {

    constructor(props) {
        super(props);
        this.state = {
            listStory: [],
            error: null,
            isLoaded: false,
        }
    }

    componentDidMount() {
        fetch('https://api-story-teller.herokuapp.com/api/stories?page=0&limit=20')
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        listStory: result
                    });
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error: error
                    });
                }
            );
    }
    render() {
        const { listStory, error, isLoaded } = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {
            return (
                <section>
                    <h2>Stories:</h2>
                    {listStory.map((story) => (
                        <Col  key={story.id} className="story-block">
                            <article className="user-story-container">
                                <aside className="user-image">
                                    <img alt="User" src="default.png"></img>
                                </aside>
                                <section className="story-description">
                                    <h4>{story.title}</h4>
                                    <div className="story-lines">
                                        <div className="story-image-container">
                                            <img alt="Default" src="default.png"></img>
                                        </div>
                                        <div className="pull-left" dangerouslySetInnerHTML={{__html: story.story}}></div>
                                    </div>
                                    <Button color="primary" className="read-more-button" hidden>Read More</Button>
                                    <Button color="primary" className="read-more-button" hidden>Read Less</Button>
                                </section>
                            </article>
                        </Col>
                    ))}
                </section>
            );
        }
    }
}
