import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input } from 'reactstrap';
import './login.css';

export class Login extends Component {
  constructor (props) {
    super(props);

    this.state = { rSelected: false };

    this.onRemmemberMeClick = this.onRemmemberMeClick.bind(this);
  }

  onRemmemberMeClick(selected) {
    this.setState({ rSelected: !selected });
  }
  render() {
    return (
      <section className="login float-right">
        <Form>
          <FormGroup>
            <Label for="emailId">Email</Label>
            <Input type="email" name="email" id="emailId" placeholder="Email" />
          </FormGroup>
          <FormGroup>
            <Label for="password">Password</Label>
            <Input type="password" name="password" id="password" placeholder="Password" />
          </FormGroup>
          <FormGroup check>
            <Label check>
              <Input type="checkbox" onClick={() => this.onRemmemberMeClick()} active={this.state.rSelected ? 'true' : 'false'}/>{' '}
              Remmember Me
          </Label>
          </FormGroup>
          <Button color="primary" size="lg" block className="form-submit-button">Submit</Button>
        </Form>
      </section>
    );
  }
}