import React, { Component } from 'react';
import { Editor, EditorState, RichUtils, getDefaultKeyBinding } from 'draft-js';
import {stateToHTML} from 'draft-js-export-html';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import './add-story.css';

export class AddStory extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            editorState: EditorState.createEmpty(),
            storyHeader: '',
            storyPicture: '',
            storyPictureName: ''
        };
        this.focus = () => this.refs.editor.focus();
        this.handleStoryChange = (editorState) => this.setState(
            { editorState }
        );
        this.handleKeyCommand = this._handleKeyCommand.bind(this);
        this.mapKeyToEditorCommand = this._mapKeyToEditorCommand.bind(this);
        this.toggleBlockType = this._toggleBlockType.bind(this);
        this.toggleInlineStyle = this._toggleInlineStyle.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleHeadingChange = this.handleHeadingChange.bind(this);
        this.handlePictureChange = this.handlePictureChange.bind(this);
    }
    _handleKeyCommand(command, editorState) {
        const newState = RichUtils.handleKeyCommand(editorState, command);
        if (newState) {
            this.handleStoryChange(newState);
            return true;
        }
        return false;
    }
    _mapKeyToEditorCommand(e) {
        if (e.keyCode === 9 /* TAB */) {
            const newEditorState = RichUtils.onTab(
                e,
                this.state.editorState,
                4, /* maxDepth */
            );
            if (newEditorState !== this.state.editorState) {
                this.handleStoryChange(newEditorState);
            }
            return;
        }
        return getDefaultKeyBinding(e);
    }
    _toggleBlockType(blockType) {
        this.handleStoryChange(
            RichUtils.toggleBlockType(
                this.state.editorState,
                blockType
            )
        );
    }
    _toggleInlineStyle(inlineStyle) {
        this.handleStoryChange(
            RichUtils.toggleInlineStyle(
                this.state.editorState,
                inlineStyle
            )
        );
    }
    handleSubmit() {
        // alert('Story Header' + this.state.storyHeader);
        // alert('Story Picture' + this.state.storyPicture);
        alert('Editor State' + stateToHTML(this.state.editorState.getCurrentContent()));
    }
    handlePicture() {
        document.getElementById('pictureId').click();
    }
    handleHeadingChange(event) {
        this.setState({storyHeader: event.target.value});
    }
    handlePictureChange(event) {
        var eventLength = event.target.value.split('\\').length;
        var eventVar = event.target.value.split('\\')[eventLength - 1];
        this.setState({storyPicture: event.target.value, storyPictureName: eventVar});
    }
    render() {
        const { editorState } = this.state;
        // If the user changes block type before entering any text, we can
        // either style the placeholder or hide it. Let's just hide it now.
        let className = 'RichEditor-editor';
        var contentState = editorState.getCurrentContent();
        if (!contentState.hasText()) {
            if (contentState.getBlockMap().first().getType() !== 'unstyled') {
                className += ' RichEditor-hidePlaceholder';
            }
        }
        return (
            <Form>
                <h2>Add Your Stories:</h2>
                <FormGroup>
                    <Label for="headingId">Header</Label>
                    <Input type="text" value={this.state.storyHeader} onChange={this.handleHeadingChange} name="heading" id="headingId" placeholder="Heading..." />
                </FormGroup>
                <FormGroup className="clear-fix">
                    <Label for="pictureId" className="clear-fix">Picture</Label>
                    <Input type="file" value={this.state.storyPicture} onChange={this.handlePictureChange} name="picture" id="pictureId" placeholder="Upload..." hidden/>
                    <Input type="text" value={this.state.storyPictureName} name="PicContainer" placeholder="Upload" className="browse-input" readOnly/>
                    <Button color="primary" onClick={this.handlePicture} className="browse-button">Browse</Button>
                </FormGroup>
                <FormGroup>
                    <Label for="story">Story</Label>
                    <div className="RichEditor-root">
                        <BlockStyleControls
                            editorState={editorState}
                            onToggle={this.toggleBlockType}
                        />
                        <InlineStyleControls
                            editorState={editorState}
                            onToggle={this.toggleInlineStyle}
                        />
                        <div className={className} onClick={this.focus}>
                            <Editor
                                blockStyleFn={getBlockStyle}
                                customStyleMap={styleMap}
                                editorState={editorState}
                                handleKeyCommand={this.handleKeyCommand}
                                keyBindingFn={this.mapKeyToEditorCommand}
                                onChange={this.handleStoryChange}
                                ref="editor"
                                spellCheck={true}
                            />
                        </div>
                    </div>
                </FormGroup>
                <Row className="mb-5">
                    <Col className="text-center">
                        <Button color="secondary">Preview</Button>
                        <Button color="primary" onClick={this.handleSubmit}>Add</Button>
                    </Col>
                </Row>
            </Form>
        );
    }
}
// Custom overrides for "code" style.
const styleMap = {
    CODE: {
        backgroundColor: 'rgba(0, 0, 0, 0.05)',
        fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
        fontSize: 16,
        padding: 2,
    },
};
function getBlockStyle(block) {
    switch (block.getType()) {
        case 'blockquote': return 'RichEditor-blockquote';
        default: return null;
    }
}
class StyleButton extends React.Component {
    constructor() {
        super();
        this.onToggle = (e) => {
            e.preventDefault();
            this.props.onToggle(this.props.style);
        };
    }
    render() {
        let className = 'RichEditor-styleButton';
        if (this.props.active) {
            className += ' RichEditor-activeButton';
        }
        return (
            <span className={className} onMouseDown={this.onToggle}>
                {this.props.label}
            </span>
        );
    }
}
const BLOCK_TYPES = [
    { label: 'H1', style: 'header-one' },
    { label: 'H2', style: 'header-two' },
    { label: 'H3', style: 'header-three' },
    { label: 'H4', style: 'header-four' },
    { label: 'H5', style: 'header-five' },
    { label: 'H6', style: 'header-six' },
    { label: 'Blockquote', style: 'blockquote' },
    { label: 'UL', style: 'unordered-list-item' },
    { label: 'OL', style: 'ordered-list-item' },
    { label: 'Code Block', style: 'code-block' },
];
const BlockStyleControls = (props) => {
    const { editorState } = props;
    const selection = editorState.getSelection();
    const blockType = editorState
        .getCurrentContent()
        .getBlockForKey(selection.getStartKey())
        .getType();
    return (
        <div className="RichEditor-controls">
            {BLOCK_TYPES.map((type) =>
                <StyleButton
                    key={type.label}
                    active={type.style === blockType}
                    label={type.label}
                    onToggle={props.onToggle}
                    style={type.style}
                />
            )}
        </div>
    );
};
var INLINE_STYLES = [
    { label: 'Bold', style: 'BOLD' },
    { label: 'Italic', style: 'ITALIC' },
    { label: 'Underline', style: 'UNDERLINE' },
    { label: 'Monospace', style: 'CODE' },
];
const InlineStyleControls = (props) => {
    const currentStyle = props.editorState.getCurrentInlineStyle();

    return (
        <div className="RichEditor-controls">
            {INLINE_STYLES.map((type) =>
                <StyleButton
                    key={type.label}
                    active={currentStyle.has(type.style)}
                    label={type.label}
                    onToggle={props.onToggle}
                    style={type.style}
                />
            )}
        </div>
    );
}
